<script>
import Header from "../../../components/Header";
import Footer from "../../../components/Footer";
import SidebarMain from "../../../components/SidebarMain";
import VueHtml2pdf from "vue-html2pdf";

export default {
  components: {SidebarMain, Footer, Header, VueHtml2pdf},
  data() {
    return {
      language: [],
      lang_name: '',
      imageUrl: [],
      myImages: [],
      files_name: [],
      options: [
        {value: '1', text: 'Elementary'},
        {value: '2', text: 'Limited Working Proficiency'},
        {value: '3', text: 'Professional Working Proficiency'},
        {value: '4', text: 'Full Professional Proficiency'},
        {value: '5', text: 'Native/Bilingual'},
      ],
      item: '',


      //resume main
      first_name: '',
      last_name: '',
      address: '',
      region: '',
      district: '',
      school: '',
      class_grade: '',
      interests: '',
      expected_position: '',
      expected_salary: '',
      avatr: '',
      graduation_date: '',
      phone_number: '',
      email: '',
      date_of_birth: '',
      type: '',
      work_experiences: '',
      certificates: '',
      languages: ''
    }
  },
  methods: {
    addJobs() {

    },
    createResume() {
      const form = new FormData();
      form.append("expected_salary", this.expected_salary);
      form.append("avatr", this.myImages[0]);
      form.append("first_name", this.first_name);
      form.append("last_name", this.last_name);
      form.append("address", this.address);
      form.append("region", this.region);
      form.append("district", this.district);
      form.append("school", this.school);
      form.append("class_grade", this.class_grade);
      form.append("interests", this.interests);
      form.append("expected_position", this.expected_position);
      form.append("graduation_date", this.graduation_date);
      form.append("phone_number", this.phone_number);
      form.append("email", this.email);
      form.append("date_of_birth", this.date_of_birth);
      form.append("type", this.type);
      form.append("work_experiences", this.work_experiences);
      form.append("certificates", this.certificates);
      form.append("languages", this.language);
      this.$axiosDefault.post(`students/${this.$store.state.me.id}/resumes/`, form, {
        headers: {
          'Authorization': `Bearer ${localStorage.getItem('reg_token')}`,
          'Accept-Language': 'oz'
        }
      })
    },
    onSelect() {
      console.log(this.item)
      this.language.push({
        "lang_name": this.lang_name,
        "lang_dagre": this.item
      })

      this.item = []
      this.lang_name = ''
    },
    generateReport() {
      this.$refs.html2Pdf.generatePdf()
    },
    trySubmitFile(e) {
      this.imageUrl = []
      this.myImages = []
      for (let i = 0; i < e.target.files.length; i++) {
        this.files_name.push(e.target.files[i])
      }
      var files = Array.from(e.target.files);
      console.log(typeof (files))
      files.forEach((file) => {
        this.myImages.push(file);
        var reader = new FileReader();
        var vm = this;
        reader.onload = function (e) {
          vm.imageUrl.push(e.target.result);
        };
        reader.readAsDataURL(file);
      });
    },
  }
}
</script>
